import { StaggeredContainer, StaggeredItem } from "../../../animations/staggeredFadeIn";
import { Button, ButtonWrapper, ContentHeader, Section } from "../styled";
import { AboutDescription, AboutContent, AboutImage, AboutWrapper, ImageWrapper } from "./styled";
import image from "./images/me.webp"
import { Link } from "react-scroll";


const About = () => (

    <Section id="about">
        <StaggeredContainer>
            <AboutWrapper>
                <AboutContent>
                    <StaggeredItem>
                        <ContentHeader>
                            O mnie
                        </ContentHeader>
                    </StaggeredItem>
                    <StaggeredItem>
                        <AboutDescription>
                            <strong>Paradise</strong> to DJ, wodzirej i&nbsp;animator w&nbsp;jednej osobie&nbsp; zadba o&nbsp;kady, nawet najmniejszy detal Waszego przyjcia.
                        </AboutDescription>
                    </StaggeredItem>
                    <StaggeredItem>
                        <AboutDescription>
                            To czowiek peen pozytywnej energii, charyzmy i&nbsp;nieszablonowych pomysw, ktry potrafi poczy dobr zabaw z&nbsp;klas i&nbsp;wyczuciem smaku.
                        </AboutDescription>
                    </StaggeredItem>
                    <StaggeredItem>
                        <AboutDescription>
                            Stawia na oryginalno, by&nbsp;kade wesele czy event byy wyjtkowe i&nbsp;zupenie inne ni wszystkie.
                        </AboutDescription>
                    </StaggeredItem>
                    <StaggeredItem>
                        <AboutDescription>
                            Profesjonalizm i rzetelno to jego priorytety &nbsp; dlatego kad wspprac opiera na zaufaniu, otwartoci i&nbsp;dokadnym poznaniu oczekiwa klientw, aby stworzy wydarzenie dopracowane w&nbsp;najmniejszym szczegle.
                        </AboutDescription>
                    </StaggeredItem>
                    <StaggeredItem>
                        <ButtonWrapper>
                            <Button as={Link} to="offer" smooth={true} duration={800} offset={-200}>
                                Poznaj ofert
                            </Button>
                        </ButtonWrapper>
                    </StaggeredItem>
                </AboutContent>

                <ImageWrapper>
                    <StaggeredItem>
                        <AboutImage src={image} alt="DJ Paradise prowadzcy wesele z owietleniem scenicznym" />
                    </StaggeredItem>
                </ImageWrapper>

            </AboutWrapper>
        </StaggeredContainer>
    </Section>
);

export default About;