import { StaggeredContainer, StaggeredItem } from "../../../animations/staggeredFadeIn";
import { Container, ContentHeader, Description, DescriptionLight, Section, SectionBackground, StyledParallaxBanner } from "../styled";
import { List, ListWrapper, OfferContent, OfferImage, OfferImageWrapper, OfferWrapper, Tile, TileDescription, TileName, TileWrapper } from "./styled";
import image from "./images/weeding.webp"
import image2 from "./images/individual.webp"
import image3 from "./images/offer.webp"


const Offer = () => (
    <Section>
        <SectionBackground>
            <Container>
                <StaggeredContainer>
                    <OfferWrapper>
                        <OfferImageWrapper>
                            <StaggeredItem>
                                <OfferImage src={image3} alt="Gocie bawicy si na parkiecie z DJ Paradise" />
                            </StaggeredItem>
                        </OfferImageWrapper>
                        <OfferContent>
                            <StaggeredItem>
                                <ContentHeader id="offer">
                                    Oferta i&nbsp;Usugi
                                </ContentHeader>
                            </StaggeredItem>
                            <StaggeredItem>
                                <DescriptionLight>
                                    Z pasj tworz niezapomniane muzyczne wspomnienia! Moje usugi DJ-skie to gwarancja profesjonalizmu i&nbsp;niezawodnoci, niezalenie od tego, czy organizujesz wymarzone wesele, prestiowy event firmowy czy jubileuszow imprez indywidualn. Poniej znajdziesz szczegowe informacje o&nbsp;tym, jak mog sprawi, e Twoje wydarzenie bdzie pene doskonaej muzyki i&nbsp;fantastycznej zabawy.
                                </DescriptionLight>
                            </StaggeredItem>
                        </OfferContent>
                    </OfferWrapper>
                </StaggeredContainer>
                <StaggeredContainer>
                    <StaggeredItem>
                        <ContentHeader>
                            Wesela
                        </ContentHeader>
                    </StaggeredItem>
                    <StaggeredItem>
                        <DescriptionLight>
                            Jako DJ weselny stawiam na profesjonalizm i&nbsp;rzetelno, bo wiem, jak wany jest to dzie dla Was i&nbsp;Waszych bliskich. Zanim przyjm zlecenie, zawsze spotykam si z&nbsp;Par Mod, by wsplnie stworzy scenariusz imprezy. Dziki temu mog idealnie dopasowa repertuar oraz atrakcje do Waszych oczekiwa i&nbsp;gustw goci, tak aby wszystko przebiego zgodnie z&nbsp;planem.
                        </DescriptionLight>
                    </StaggeredItem>
                    <ListWrapper>
                        <StaggeredItem>
                            <ContentHeader>Co oferuj</ContentHeader>
                        </StaggeredItem>
                        <List>
                            <StaggeredItem>
                                <Tile>
                                    <TileName>Profesjonalne nagonienie</TileName>
                                    <TileDescription>gwarantuj czysty, wysokiej jakoci dwik, ktry wypeni ca sal, a&nbsp;poziom gonoci bdzie dostosowany do charakteru imprezy</TileDescription>
                                </Tile>
                            </StaggeredItem>
                            <StaggeredItem>
                                <Tile>
                                    <TileName>Owietlenie parkietu i&nbsp;owietlenie RETRO</TileName>
                                    <TileDescription>tworzenie wyjtkowej atmosfery jest dla mnie priorytetem, dlatego zadbam o&nbsp;odpowiednie efekty wietlne, ktre podkrel magiczny klimat Waszego wesela</TileDescription>
                                </Tile>
                            </StaggeredItem>
                            <StaggeredItem>
                                <Tile>
                                    <TileName>Animacje, zabawy, konkursy i&nbsp;oczepiny</TileName>
                                    <TileDescription>dbam o&nbsp;dobr zabaw i&nbsp;angauj goci w&nbsp;rozmaite aktywnoci, ktre sprawi, e Wasze wesele bdzie pene radoci i&nbsp;miechu</TileDescription>
                                </Tile>
                            </StaggeredItem>
                        </List>
                        <TileWrapper>
                            <StaggeredItem>
                                <Tile>
                                    <TileName>Darmowe gadety i&nbsp;rekwizyty</TileName>
                                    <TileDescription>oferuj rnorodne akcesoria, takie jak paki LED, ktre urozmaic animacje i&nbsp;zachc wszystkich do wsplnej zabawy</TileDescription>
                                </Tile>
                            </StaggeredItem>
                            <StaggeredItem>
                                <Tile>
                                    <TileName>Repertuar dopasowany do Waszych potrzeb</TileName>
                                    <TileDescription>kad playlist ustalam z Par Mod, aby gra utwory, ktre idealnie pasuj do Waszego stylu i&nbsp;sprawi, e parkiet bdzie peen do samego koca. Muzyka na yczenie? Oczywicie, to dla mnie aden problem!</TileDescription>
                                </Tile>
                            </StaggeredItem>
                        </TileWrapper>
                    </ListWrapper>
                </StaggeredContainer>
            </Container>
            <StyledParallaxBanner
                layers={[{
                    image: image, speed: -30,
                    style: {
                        backgroundPosition: '40% center',
                    },
                }]}
            />
        </SectionBackground>
        <Container>
            <StaggeredContainer>
                <StaggeredItem>
                    <ContentHeader>
                        Eventy firmowe i&nbsp;imprezy indywidualne
                    </ContentHeader>
                </StaggeredItem>
                <StaggeredItem>
                    <Description>
                        Szukasz profesjonalnej oprawy muzycznej na firmowy event, gal, konferencj, jubileusz lub imprez integracyjn? Jako dowiadczony DJ specjalizuj si w&nbsp;tworzeniu wyjtkowej atmosfery podczas wydarze biznesowych&nbsp; od eleganckich bankietw po dynamiczne imprezy taneczne.
                    </Description>
                </StaggeredItem>
            </StaggeredContainer>
            <StaggeredContainer>
                <ListWrapper>
                    <StaggeredItem>
                        <ContentHeader>Co oferuj</ContentHeader>
                    </StaggeredItem>
                    <List>
                        <StaggeredItem>
                            <Tile>
                                <TileName>Oprawa muzyczna dopasowana do charakteru wydarzenia</TileName>
                                <TileDescription $variant="light">Repertuar zawsze dostosowuj do potrzeb i profilu eventu  od subtelnego ta muzycznego po energetyczne sety na parkiecie</TileDescription>
                            </Tile>
                        </StaggeredItem>
                        <StaggeredItem>
                            <Tile>
                                <TileName>Profesjonalny sprzt nagonieniowy i&nbsp;owietleniowy</TileName>
                                <TileDescription $variant="light">Korzystam z wysokiej klasy sprztu, ktry gwarantuje czysty dwik i&nbsp;efektown opraw wizualn niezalenie od wielkoci sali</TileDescription>
                            </Tile>
                        </StaggeredItem>
                        <StaggeredItem>
                            <Tile>
                                <TileName>Prowadzenie imprezy (opcjonalnie)</TileName>
                                <TileDescription $variant="light">Na yczenie mog peni rwnie funkcj konferansjera&nbsp; z&nbsp;wyczuciem, humorem i&nbsp;odpowiednim stylem</TileDescription>
                            </Tile>
                        </StaggeredItem>
                        <StaggeredItem>
                            <Tile>
                                <TileName>Indywidualne podejcie</TileName>
                                <TileDescription $variant="light">Kady event traktuj indywidualnie&nbsp; chtnie wysucham Twoich potrzeb i&nbsp;przygotuj dopasowan ofert</TileDescription>
                            </Tile>
                        </StaggeredItem>
                        <StaggeredItem>
                            <Tile>
                                <TileName>Dodatkowe atrakcje (na yczenie)</TileName>
                                <TileDescription $variant="light">Wsppracuj z muzykami, wokalistami oraz specjalistami od multimediw&nbsp; wsplnie moemy stworzy wyjtkowe widowisko</TileDescription>
                            </Tile>
                        </StaggeredItem>
                        <StaggeredItem>
                            <Tile>
                                <TileName>Zadbam o&nbsp;opraw muzyczn, klimat i&nbsp;emocje</TileName>
                                <TileDescription $variant="light">a&nbsp;Twoi gocie bd wspomina ten event jeszcze dugo po jego zakoczeniu</TileDescription>
                            </Tile>
                        </StaggeredItem>
                    </List>
                </ListWrapper>
            </StaggeredContainer>
        </Container>
        <SectionBackground>
            <StyledParallaxBanner
                layers={[{ image: image2, speed: -30 }]}
            />
        </SectionBackground>
    </Section >
);

export default Offer;